<template >
  <div class="overflow-auto">
    <h1>게시판 목록</h1>
    <b-table 
      striped hover 
      :items="boardList"
      :per-page="perPage"
      :current-page="currentPage"
      :sort-by.sync="boardTime"
      :sort-desc.sync="sortDesc"
      small
      @row-clicked="toBoard"
      :fields="fields"
      >
      <template #cell(index)="data">
        <router-link :to="{name: 'boardInfo', params: {id: data.index.id},}">
        </router-link>
        {{ data.index + 1 }}
      </template>
    </b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="my-table"
    ></b-pagination>
    <p class="mt-3">Current Page: {{ currentPage }}</p>
    <hr>
    <v-btn @click ="toCreate">게시글 작성</v-btn>
  </div>
    
</template>

<script>
export default {
  data() {
    return {
      boardList: [],
      perPage: 10,
      currentPage: 1,
      sortDesc: true,
      fields: [
        'index',
        {key: 'boardTitle'},
        {key: 'boardTime'},
        {key: 'userNickname'}
      ],
    }
  },
  methods: {
    board() {
      this.$axios.get("http://localhost:8080/board/list").then((res) => {
        if (res.status == 200) {
          this.boardList = res.data;
          console.log(res.data)
        }
      })
    },
    toCreate() {
        this.$router.push("/boardcreate");
    },
    toBoard(index) {
        this.$router.push({name: 'boardInfo', params: {id: index.id}})
    },
  },
  mounted() {
    this.board();
  },
  computed: {
    rows() {
      return this.boardList.length;
    }
  }
}
</script>


