<template>
  
  <div >
	<h1>회원 목록</h1>
	
	<b-table 
	  striped hover 
	  :items="userList"
	  :per-page="perPage"
	  :current-page="currentPage"
		:fields="fields"
	  small
		@row-clicked="toUser"
	  >
		<template #cell(index)="data">
			<router-link :to="{name: 'userInfo', params: {id: data.index.id},}">
			</router-link>
			{{ data.index + 1 }}
		</template>
	</b-table>
	<b-pagination
		v-model="currentPage"
		:total-rows="rows"
		:per-page="perPage"
		aria-controls="my-table"
		></b-pagination>
	<p class="mt-3">Current Page: {{ currentPage }}</p>
  </div>
</template>



<script>

export default {
  data() {
		return {
			userList: [],
			perPage: 10,
			currentPage: 1,
			fields: [
        'index',
        {key: 'userId'},
        {key: 'userNickname'},
        {key: 'userEmail'}
      ],
		}
  },
  
  methods: {
		user() {
			try {
				this.$axios.get("http://localhost:8080/user/list")
					.then((res) => {
				if (res.status == 200) {
					this.userList = res.data;
					console.log(res.data)
				} 
				})
			} catch (error) {
				alert("error")
			}
		},
		toCreate() {
			this.$router.push("/boardcreate");
		},
		toUser(index) {
      this.$router.push({name: 'userInfo', params: {id: index.id}})
    },
	},
	created() {
		this.user();
	},

	computed: {
		rows() {
			return this.userList.length;
		},
  }
}
</script>