<template>

  <div style="text-align: center">
    <h1>{{ user.userNickname }}'s Profile Update</h1>
      <b-card 
        style ="width: 400px; display:inline-block">
        <b-card-title>NAME</b-card-title>
        <b-card-subtitle>org : {{ user.userName }}</b-card-subtitle>
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="userName"
          placeholder=Name />
        <hr>
        <b-card-title>PASSWORD</b-card-title>
        <input 
          class="form-control"
          type="password"
          maxlength="20"
          v-model="userPassword"
          placeholder="Password"/>
        <hr>
        <b-card-title>NICKNAME</b-card-title>
        <b-card-subtitle>org : {{ user.userNickname }}</b-card-subtitle>
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="userNickname"
          placeholder="Nickname"/>
        <hr>
        <b-card-title>EMAIL</b-card-title>
        <b-card-subtitle>org : {{ user.userEmail }}</b-card-subtitle>
        <input
          class="form-control"
          type="text"
          maxlength="20"
          v-model="userEmail"
          placeholder="Email"/>
        <hr>
        <b-card-title>PHONE NUMBER</b-card-title>
        <b-card-subtitle>org : {{ user.userPhonenumber }}</b-card-subtitle>
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="userPhonenumber"
          placeholder="Phonenumber"/>
        <hr>
        <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=1 v-if="$store.state.userpermission==1">관리자</b-form-radio>
        <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=0 v-if="$store.state.userpermission==1">사용자</b-form-radio>
        <v-btn class="me-4" type="submit" @click="userUpdate">UPDATE</v-btn>
      </b-card>

  </div>

</template>


<script>
export default {
  data() {
    return {
      user: [],
      id: this.$route.params.index || this.$store.state.userindex,  //리스트를 통해 들어왔을 경우 || 유저가 직접 들어왔을 경우
      userName: "",
      userPassword: "",
      userEmail: "",
      userNickname: "",
      userPhonenumber: "",
      userPermission: 0,
    }
  },
  methods: {
    getUser(){
      this.$axios.get("http://localhost:8080/user/read/" + this.id).then((res) => {
        this.user = res.data
      })
      .catch((error) => {
        console.log(error);
      })
    },
    userUpdate() {
      const formData = new FormData();
      formData.append("id", this.user.id)
      formData.append("userPassword" , this.userPassword)
      formData.append("userName" , this.userName)
      formData.append("userPhonenumber" , this.userPhonenumber)
      formData.append("userEmail" , this.userEmail)
      formData.append("userNickname" , this.userNickname)
      formData.append("userPermission", this.userPermission)

      this.$axios.put("http://localhost:8080/user/update", formData).then((res)=> {

        if (this.$store.state.userpermission == '1') {
          this.$router.push({name: "userInfo", params: {id: this.user.id}});

        } else if(this.$store.state.userpermission == '0') {
          this.$store.commit('setNickname', this.userNickname);
          this.$store.commit('setPermission', this.userPermission);
          this.$router.push("/userinfo");
        }
      })
    }
  },
  mounted() {
    this.getUser();
  },
};
</script>