<template>

  <div style="text-align: center">
    <h1>{{ user.userNickname }}'s Profile</h1>
    <b-card 
      style ="width: 400px; display:inline-block"
    >
      
      <b-card-title>NAME</b-card-title>
      <b-card>{{ user.userName }}</b-card>
      <hr>
      <b-card-title>USER ID</b-card-title>
      <b-card>{{ user.userId }}</b-card>
      <hr>
      <b-card-title>NICKNAME</b-card-title>
      <b-card>{{ user.userNickname }}</b-card>
      <hr>
      <b-card-title>EMAIL</b-card-title>
      <b-card>{{ user.userEmail }}</b-card>
      <hr>
      <b-card-title>PHONE NUMBER</b-card-title>
      <b-card>{{ user.userPhonenumber }}</b-card>
      <hr>
      <b-card-text>Change your information. <span @click="toUserUpdate">UPDATE</span></b-card-text>

      <!-- 회원 탈퇴는 관리자만 가능 -->
      <b-card-text @click="toDelete" v-if="$store.state.userpermission == 1">회원 탈퇴</b-card-text> 
    </b-card> 
    
  </div>

</template>

<script>
// 유저가 접근하는 것
// 관리자가 접근하는 것

export default {
  data() {
    return {
      user: {},
      id: this.$route.params.id || this.$store.state.userindex,
      update: null
    }
  },
  methods: {
    getUser(){
      this.$axios.get("http://localhost:8080/user/read/" + this.id).then((res) => {
        console.log(res.data.id)
        this.update = res.data.id
        this.user= res.data
      })
      .catch((error) => {
        console.log(error);
      })
    },
    toUserUpdate() {
      this.$router.push({name: 'userUpdate', params: {index: this.update}})
    },
    toDelete() {
      this.$axios.delete("http://localhost:8080/user/delete/" + this.id).then((res) => {
        alert("삭제되었습니다.")
        this.$router.push("/userlist")
      })
    }
  },
  mounted() {
    this.getUser();
  },
};
</script>