<template>
  <div id="login_content">
    <h2 class="mb-3" >회원정보</h2>
    <v-text-field
      v-model="userId"
      label="User ID"
      :error-messages="hello"
    >
    </v-text-field>
    <v-text-field
      v-model="userPassword"
      label="PASSWORD"
      type="password"
    >
    </v-text-field>
    <v-text-field
      v-model="userName"
      label="NAME"
    >
    </v-text-field>
    <v-text-field 
      v-model="userPhonenumber"
      label="PHONENUMBER"
    >
    </v-text-field>
    <v-text-field
      v-model="userEmail"
      label="EMAIL"
      type="email"
    >
    </v-text-field>
    <v-text-field
      v-model="userNickname"
      label="NICKNAME"
    >
    </v-text-field>
    <button type="submit" class="mt-4 btn-pers" id="login_button" @click="signSubmit" >
      회원가입
    </button>
  </div>
</template>

<script>

export default {
  


  data() {
    return {
      userId: null,
      userPassword: null,
      userName: null,
      userPhonenumber: null,
      userEmail: null,
      userNickname: null,
    }
  },
  methods: {
    signSubmit() {
    let saveData = {};
    saveData.userId = this.userId;
    saveData.userPassword = this.userPassword;
    saveData.userName = this.userName;
    saveData.userPhonenumber = this.userPhonenumber;
    saveData.userEmail = this.userEmail;
    saveData.userNickname = this.userNickname;
    try {
      this.$axios.post("http://localhost:8080/user/create", JSON.stringify(saveData), {
        headers: {
          "Content-Type" : `application/json`,
        },
        body: {
          userId : this.userId,
          userPassword : this.userPassword,
          userName : this.userName,
          userPhonenumber : this.userPhonenumber,
          userNickname : this.userNickname,
          userEmail : this.userEmail,
          userPermission : 0,
        }
    })
    .then((res) => {
      if (res.status == 200) {
        alert("Sign Up");
        this.$router.push("/");
      }
    });
    } catch (error) {
      console.error(error);
    }
    },
  },
  setup() {

  }
}
</script>